.resumeEducationItem {
  display: flex;
  gap: 30px;
  align-items: center;
}

.info {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.info>.university {
  font-weight: bold;
  font-size: 16px;
}

.info>.degree, .info>.subject {
  font-style: italic;
  font-size: 14px;
}